   generic.RESET
------------------------------------------ */
* {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
dl, dd, ol, ul,
form, fieldset, legend,
table, th, td, caption,
hr, figure {
  margin: 0;
  padding: 0; }

/* ------------------------------------------
   base.BODY
------------------------------------------ */
body.home {
  background-color: #1C1D1E;
  color: #fff; }

body.post {
  background-color: #FAFAFA;
  color: #1C1D1E; }

/* ------------------------------------------
   base.TYPOGRAPHY
------------------------------------------ */
body {
  font-family: "Space Mono", sans-serif; }

/* ------------------------------------------
   objects.WRAP
------------------------------------------ */
.wrap {
  position: relative;
  max-width: 1480px;
  padding: 0 20px 0 20px;
  margin: 0 auto; }

.wrap--narrow {
  max-width: 840px; }

/**
