  display: inline-block;
  /* 1 */
  fill: currentColor;
  /* 2 */
  height: 1em;
  /* 3 */
  position: relative;
  /* 4 */
  top: -0.0625em;
  /* 4 */
  vertical-align: middle;
  /* 4 */
  width: 1em;
  /* 3 */ }

.global-header {
  width: 100%;
  overflow-x: hidden;
  position: fixed;
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.5);
  min-height: 85px;
  z-index: 5000; }
  .global-header > .wrap {
    display: flex;
    align-content: center; }
    .global-header > .wrap > * {
      display: flex;
      align-items: center;
      height: 85px; }

.global-header__left {
  flex: 1 0 15%;
  background-color: #20242A;
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.5);
  z-index: 10;
  justify-content: flex-end;
  padding-right: 30px; }
  .global-header__left:before {
    content: '';
    display: block;
    background-color: #20242A;
    width: 100%;
    top: 0;
    height: 85px;
    position: absolute;
    left: -100%;
    margin-left: 20px; }

.global-header__center {
  background-color: rgba(32, 36, 42, 0.85);
  flex: 1 0 70%;
  justify-content: center; }

.global-header__right {
  flex: 1 0 15%;
  background-color: #20242A;
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.5);
  z-index: 10;
  justify-content: flex-start;
  padding-left: 30px; }
  .global-header__right:before {
    content: '';
    display: block;
    background-color: #20242A;
    width: 100%;
    top: 0;
    height: 85px;
    position: absolute;
    right: -100%;
    margin-right: 20px; }

.global-header__logo img {
  display: block; }

.global-header__caption {
  text-align: center;
  font-size: 0.625rem;
  color: #FF2942;
  letter-spacing: .1em; }

.global-header__nav ul {
  list-style: none;
  display: flex;
  justify-content: center; }

.global-header__nav a {
  padding: 10px 15px;
  color: #C9D6EF;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 0.8125rem;
  letter-spacing: .1em; }

.global-header__alt-menu ul {
  list-style: none;
  display: flex; }

.global-header__alt-menu a {
  padding: 5px; }
  .global-header__alt-menu a:hover .icon {
    color: #0E64FF; }

.global-header__alt-menu .icon {
  height: 20px;
  width: 20px;
  color: #fff; }

.progressbar .wrap {
  display: flex;
  align-content: center; }

.global-header__progress-left {
  flex: 1 0 15%;
  z-index: 10;
  padding-right: 30px; }

.global-header__progress-center {
  flex: 1 0 70%;
  justify-content: center;
  position: relative; }

.global-header__progress-right {
  flex: 1 0 15%;
  z-index: 10;
  padding-left: 30px; }

.global-header__progress-container {
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: transparent;
  bottom: 0;
  left: 0;
  z-index: 5001; }

.global-header__progress-bar {
  position: absolute;
  float: left;
  height: 100%;
  background-color: #FF2942; }

.single-post__header {
  height: 570px;
  background-position: center;
  background-size: cover; }
